import { cva } from 'cva'
import type { ClassRecord } from '@/shared/lib/style'
import type { Variant } from './types'

export const alert = cva({
  base: 'relative flex gap-3 rounded-md p-4 text-sm',
  variants: {
    variant: {
      info: 'bg-blue-50 text-blue-700',
      danger: 'bg-red-50 text-red-700',
      success: 'bg-green-50 text-green-700',
      warn: 'bg-yellow-50 text-yellow-700',
    } satisfies ClassRecord<Variant>,
    border: {
      false: '',
      true: 'border-l-4',
    },
  },
  compoundVariants: [
    { variant: 'warn', border: true, class: 'border-yellow-400' },
    { variant: 'danger', border: true, class: 'border-red-400' },
    { variant: 'info', border: true, class: 'border-blue-400' },
    { variant: 'success', border: true, class: 'border-green-400' },
  ],
})

export const alertIcon = cva({
  base: 'h-5 w-5',
  variants: {
    variant: {
      info: 'text-blue-400',
      danger: 'text-red-400',
      success: 'text-green-400',
      warn: 'text-yellow-400',
    } satisfies ClassRecord<Variant>,
  },
})

export const alertHeading = cva({
  base: 'font-medium',
  variants: {
    variant: {
      info: 'text-blue-800',
      danger: 'text-red-800',
      success: 'text-green-800',
      warn: 'text-yellow-800',
    } satisfies ClassRecord<Variant>,
  },
})

export const alertClose = cva({
  base: `
  absolute right-2 top-2 size-8 rounded-md p-1.5 transition
  duration-300 focus:outline-none focus:ring-2 focus:ring-offset-2
  `,
  variants: {
    variant: {
      info: `
      focus:ring-blue-600 focus:ring-offset-blue-50
      hover:bg-blue-100 bg-blue-50 text-blue-500
      `,
      danger: `
      focus:ring-red-600 focus:ring-red-yellow-50
      hover:bg-red-100 bg-red-50 text-red-500
      `,
      success: `
      focus:ring-green-600 focus:ring-offset-green-50
      hover:bg-green-100 bg-green-50 text-green-500
      `,
      warn: `
      focus:ring-yellow-600 focus:ring-offset-yellow-50
      hover:bg-yellow-100 bg-yellow-50 text-yellow-500
      `,
    } satisfies ClassRecord<Variant>,
  },
})
