<script setup lang="ts">
import { type Component, type HTMLAttributes, computed, toRefs } from 'vue'
import { XMarkIcon } from '@heroicons/vue/20/solid'

import { type ClassValue, cn } from '@/shared/lib/style'

import type { Variant } from './types'
import { alert, alertClose, alertHeading, alertIcon } from './lib'
import { icons } from './config'

export interface Props extends /* @vue-ignore */ HTMLAttributes {
  class?: ClassValue
  variant?: Variant
  border?: boolean
  iconless?: boolean
  closable?: boolean
}

const props = withDefaults(defineProps<Props>(), { variant: 'info' })

export interface Slots {
  default?: () => any
  icon?: () => any
  heading?: () => any
}

const $slots = defineSlots<Slots>()

export type Emits = {
  close: []
}

defineEmits<Emits>()

const { variant } = toRefs(props)

const iconComponent = computed<Component>(() => icons[variant.value])
</script>

<template>
  <div :class="cn(alert({ variant, border }), props.class)">
    <slot v-if="!iconless" name="icon">
      <component :is="iconComponent" :class="alertIcon({ variant })" />
    </slot>

    <div class="flex flex-col gap-2" :class="closable ? 'mr-10' : ''">
      <div v-if="$slots.heading" :class="alertHeading({ variant })">
        <slot name="heading" />
      </div>

      <slot />
    </div>

    <button
      v-if="closable"
      :class="alertClose({ variant })"
      aria-label="close"
      @click="$emit('close')"
    >
      <XMarkIcon
        aria-hidden="true"
        class="h-5 w-5"
      />
    </button>
  </div>
</template>
